html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFE9E8;
  font-family: 'Lato', 'Helvetica Neue', Helvetica, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}
h1
{
    margin-top: 0;
}
h2
{
    margin-bottom: 0;
}
hr
{
    margin-top: 20px;
    margin-bottom: 20px;
}
.ms-Button--primary
{
    background-color: #9C1C6B !important;
}
.ms-Button--primary:hover
{
    background-color: #CA278C !important;   
}
.ms-TextField.is-active .ms-TextField-fieldGroup 
{
    border-color: #9C1C6B !important
}
.ms-Spinner-circle {
    border-color: #9C1C6B #CA278C #CA278C !important;
}
.ms-Spinner-label {
    color: #9C1C6B !important
}
.Application
{ 
    display: -webkit-flex; 
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 5vw;
}
.Application>.Logo
{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    text-decoration: none;  
}
.Application>.Logo>.Image
{
    height: 100px;
    width: 100px;
}
.Application>.Logo>.Brand
{
    font-size: 2rem;
    color: #E47297;
    text-decoration: none;
    margin-left: 20px;
    font-family: 'ShorelinesScriptBold', sans-serif;
}
.Application>nav
{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-top: 20px;
    width: 35vw;
    background-color: white;
    padding-left: 30px;
    padding-right: 30px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
}
.Application>nav>a
{
    color: #999;
    padding: 10px;
    text-decoration: none;
}
.Application>nav>a.active, .Application>nav>a:hover
{
    color: #E47297;
    border-bottom: 3px solid #E47297;
}
.Application>.Container
{
    width: 35vw;
    padding: 30px;
    margin-top: 10px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
    background-color: white;
}
@media screen and (max-width: 1200px){
    .Application>.Container, .Application>nav
    {
        width: 45vw;
    }
}
@media screen and (max-width: 820px){
    .Application>.Container, .Application>nav
    {
        width: 60vw;
    }
}
@media screen and (max-width: 620px){
    .Application>.Container, .Application>nav
    {
        width: 80%;
    }
}
.Estimate>.Title {
    text-align: center;
}
.Estimate>.Container {
    display: -webkit-flex;
    display: flex;
}
.Estimate>.Container>img {
    max-height: 200px;
}
.Estimate>.Container>.Information {
    margin-left: 10px;
}
.Estimate>.Container>.Information>.Name {
    display: block;
    font-size: 1.5rem;
}
.Estimate>.Container>.Information>span {
    display: block;
    font-size: 1rem;
}
.Dropzone
{
    border: 2px dashed #E47297;
    border-radius: 5px;
}
.Dropzone>.Message
{
    cursor: pointer;
    font-weight: 400;
    text-align: center;
    margin: 2em 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.Dropzone input[type=file]
{
    display: none;
}
.Dropzone .Preview img
{
    margin: 0 auto;
    max-width: 90%;
    display: block;
}
.SelectionBox
{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.SelectionBox>.Selection
{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 120px;
    width: 120px;
    background-color: #fcfcfc;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
    transition: box-shadow 0.5s ease;
    text-align: center;
}
.SelectionBox>.Selection.Active
{
    background-color: #eee;
}
.SelectionBox>.Selection:hover
{
    cursor: pointer;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);
}
.SelectionBox>.Selection>*
{
    display: block;
}
.Progress
{
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 2.5em;
    width: 75%;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 20px;
}
.Progress>li
{
    display: inline-block;
    position: relative;
}
.Step {
    z-index: 20;
    width: 2.5em;
    height: 2.5em;
    border-radius: 2.5em;
    background: #FBC7C4;
    color: #fff;
    line-height: 2.5em;
    text-align: center;
    transition: all 0.1s ease;
}
.Stepline
{
    z-index: 10;
    width: 2.5em;
    height: .4em;
    margin: 0 -1px;
    background: #FBC7C4;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    transition: all 0.5s ease;
}
.Stepline.Completed, .Step.Completed
{
    background: #E47297;
}
.Step.Active
{
    height: 3em;
    width: 3em;
    line-height: 3em;
}
.Step>span.Name
{
    font-weight: bold;
    position: absolute;
    color: #E47297; 
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -135%);
            transform: translate(-50%, -135%);
    display: none;
}
.Step.Active>span.Name
{
    display: block;
}
.About img
{
    border-radius: 50%;
}
.About>img
{
    display: block;
    margin: 0 auto;
}

